<template>
    <div class="page-container">
        <div class="page-header">
            <div class="header-content">
                <h1>Ê¥óÊä§ËÆ∞ÂΩï</h1>
                <p>ËÆ∞ÂΩïÂñµÊòü‰∫∫ÁöÑÁæéÂÆπÊä§ÁêÜ</p>
            </div>
            <div class="add-buttons">
                <button class="add-btn" @click="showAddDialog">
                    <i class="el-icon-plus"></i>
                    Ê∑ªÂä†ËÆ∞ÂΩï
                </button>
            </div>
        </div>

        <!-- ÂÆ†Áâ©Á≠õÈÄâ -->
        <div class="pets-filter">
            <div class="pets-list">
                <div v-for="pet in pets" :key="pet.id" class="pet-item" :class="{ active: selectedPetId === pet.id }"
                    @click="selectPet(pet.id)">
                    <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
                    <span class="pet-name">{{ pet.name }}</span>
                </div>
            </div>
        </div>

        <!-- Á±ªÂûãÁ≠õÈÄâ -->
        <div class="type-filter">
            <div v-for="type in groomingTypes" :key="type.value" class="type-tag"
                :class="{ active: selectedTypes.includes(type.value) }" @click="toggleType(type.value)">
                <span class="type-icon">{{ type.icon }}</span>
                <span class="type-label">{{ type.label }}</span>
            </div>
        </div>

        <!-- ËÆ∞ÂΩïÂàóË°® -->
        <div class="record-list">
            <div v-for="record in filteredRecords" :key="record.id" class="record-card">
                <div class="record-date">{{ formatDate(record.date) }}</div>
                <div class="record-content">
                    <div class="record-header">
                        <div class="pet-info">
                            <img :src="getPetAvatar(record.petId)" :alt="getPetName(record.petId)" class="pet-avatar">
                            <span class="pet-name">{{ getPetName(record.petId) }}</span>
                        </div>
                        <div class="record-type" :class="record.type">
                            <span class="type-icon">{{ getTypeIcon(record.type) }}</span>
                            <span class="type-label">{{ getTypeLabel(record.type) }}</span>
                        </div>
                    </div>
                    <div class="record-body">
                        <h3 class="record-title">{{ record.groomingType }}</h3>
                        <div class="record-description">
                            <div class="grooming-duration" v-if="record.duration">
                                <i class="el-icon-time"></i>
                                <span>Êó∂ÈïøÔºö{{ record.duration }}</span>
                            </div>
                            <div class="grooming-cost" v-if="record.cost">
                                <i class="el-icon-money"></i>
                                <span>Ë¥πÁî®Ôºö¬•{{ record.cost }}</span>
                            </div>
                        </div>
                        <div class="record-notes" v-if="record.notes">
                            <i class="el-icon-notebook-2"></i>
                            <span>{{ record.notes }}</span>
                        </div>
                    </div>
                    <div class="record-actions">
                        <button class="action-btn edit" @click="editRecord(record)">
                            <span class="icon">‚úèÔ∏è</span>
                            <span>ÁºñËæë</span>
                        </button>
                        <button class="action-btn delete" @click="deleteRecord(record)">
                            <span class="icon">üóëÔ∏è</span>
                            <span>Âà†Èô§</span>
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Ê∑ªÂä†/ÁºñËæëËÆ∞ÂΩïÂØπËØùÊ°Ü -->
        <div v-if="showDialog" class="dialog-overlay" @click.self="closeDialog">
            <div class="dialog-content">
                <div class="dialog-header">
                    <h2>{{ dialogTitle }}</h2>
                    <button class="close-btn" @click="closeDialog">&times;</button>
                </div>
                <div class="dialog-body">
                    <div class="form-group">
                        <label>ÈÄâÊã©ÂÆ†Áâ©</label>
                        <div class="pets-select">
                            <div v-for="pet in pets" :key="pet.id" class="pet-option"
                                :class="{ active: form.petId === pet.id }" @click="form.petId = pet.id">
                                <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
                                <span class="pet-name">{{ pet.name }}</span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label>Êó•Êúü</label>
                        <input type="date" class="form-input" v-model="form.date"
                            :max="new Date().toISOString().split('T')[0]">
                    </div>
                    <div class="form-group">
                        <label>Ê¥óÊä§Á±ªÂûã</label>
                        <div class="type-select">
                            <div v-for="type in groomingTypes" :key="type.value" class="type-option"
                                :class="{ active: form.type === type.value }" @click="form.type = type.value">
                                <span class="type-icon">{{ type.icon }}</span>
                                <span class="type-label">{{ type.label }}</span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label>ÂÖ∑‰ΩìÈ°πÁõÆ</label>
                        <input type="text" class="form-input" v-model="form.groomingType" placeholder="‰æãÂ¶ÇÔºöÊ¥óÊæ°„ÄÅÂâ™ÊåáÁî≤„ÄÅÊ¢≥ÊØõÁ≠â">
                    </div>
                    <div class="form-group">
                        <label>Êó∂Èïø</label>
                        <input type="text" class="form-input" v-model="form.duration" placeholder="‰æãÂ¶ÇÔºö1Â∞èÊó∂">
                    </div>
                    <div class="form-group">
                        <label>Ë¥πÁî®</label>
                        <input type="number" class="form-input" v-model="form.cost" placeholder="ËØ∑ËæìÂÖ•Ë¥πÁî®ÈáëÈ¢ù">
                    </div>
                    <div class="form-group">
                        <label>Â§áÊ≥®</label>
                        <textarea class="form-textarea" v-model="form.notes" placeholder="Ê∑ªÂä†‰∏Ä‰∫õÂ§áÊ≥®‰ø°ÊÅØ..."></textarea>
                    </div>
                </div>
                <div class="dialog-footer">
                    <button class="cancel-btn" @click="closeDialog">ÂèñÊ∂à</button>
                    <button class="confirm-btn" @click="saveRecord">‰øùÂ≠ò</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Grooming',
    data() {
        return {
            pets: [
                {
                    id: 1,
                    name: 'Â∞èÁÅ∞ÁÅ∞',
                    avatar: require('@/assets/normal_cat.png')
                },
                {
                    id: 2,
                    name: 'Ëä±Ëä±',
                    avatar: require('@/assets/cute_cat.png')
                }
            ],
            groomingTypes: [
                { value: 'bath', label: 'Ê¥óÊæ°', icon: 'üöø' },
                { value: 'grooming', label: 'ÁæéÂÆπ', icon: '‚úÇÔ∏è' },
                { value: 'nail', label: 'Ââ™ÊåáÁî≤', icon: 'üíÖ' },
                { value: 'brushing', label: 'Ê¢≥ÊØõ', icon: 'ü™Æ' },
                { value: 'other', label: 'ÂÖ∂‰ªñ', icon: '‚ú®' }
            ],
            selectedPetId: null,
            selectedTypes: [],
            groomingRecords: [
                {
                    id: 1,
                    date: '2024-03-20',
                    petId: 1,
                    type: 'bath',
                    groomingType: 'Ê¥óÊæ°',
                    duration: '1Â∞èÊó∂',
                    cost: 150,
                    notes: '‰ΩøÁî®‰∫ÜÊ∏©ÂíåÁöÑÁå´Âí™‰∏ìÁî®Ê≤êÊµ¥Èú≤'
                },
                {
                    id: 2,
                    date: '2024-03-15',
                    petId: 2,
                    type: 'grooming',
                    groomingType: 'ÁæéÂÆπ',
                    duration: '2Â∞èÊó∂',
                    cost: 200,
                    notes: '‰øÆÂâ™‰∫ÜÊØõÂèëÔºåÂÅö‰∫ÜÈÄ†Âûã'
                }
            ],
            showDialog: false,
            isEditing: false,
            form: {
                petId: null,
                date: '',
                type: '',
                groomingType: '',
                duration: '',
                cost: '',
                notes: ''
            }
        }
    },
    computed: {
        dialogTitle() {
            return this.isEditing ? 'ÁºñËæëËÆ∞ÂΩï' : 'Ê∑ªÂä†ËÆ∞ÂΩï'
        },
        filteredRecords() {
            let records = [...this.groomingRecords]

            if (this.selectedPetId) {
                records = records.filter(record => record.petId === this.selectedPetId)
            }

            if (this.selectedTypes.length > 0) {
                records = records.filter(record => this.selectedTypes.includes(record.type))
            }

            return records.sort((a, b) => new Date(b.date) - new Date(a.date))
        }
    },
    methods: {
        selectPet(petId) {
            this.selectedPetId = this.selectedPetId === petId ? null : petId
        },
        toggleType(type) {
            const index = this.selectedTypes.indexOf(type)
            if (index === -1) {
                this.selectedTypes.push(type)
            } else {
                this.selectedTypes.splice(index, 1)
            }
        },
        getPetAvatar(petId) {
            const pet = this.pets.find(p => p.id === petId)
            return pet ? pet.avatar : ''
        },
        getPetName(petId) {
            const pet = this.pets.find(p => p.id === petId)
            return pet ? pet.name : ''
        },
        getTypeIcon(type) {
            const typeInfo = this.groomingTypes.find(t => t.value === type)
            return typeInfo ? typeInfo.icon : '‚ú®'
        },
        getTypeLabel(type) {
            const typeInfo = this.groomingTypes.find(t => t.value === type)
            return typeInfo ? typeInfo.label : 'ÂÖ∂‰ªñ'
        },
        formatDate(date) {
            return new Date(date).toLocaleDateString('zh-CN', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
                weekday: 'long'
            })
        },
        showAddDialog() {
            this.isEditing = false
            this.form = {
                petId: null,
                date: new Date().toISOString().split('T')[0],
                type: '',
                groomingType: '',
                duration: '',
                cost: '',
                notes: ''
            }
            this.showDialog = true
        },
        editRecord(record) {
            this.isEditing = true
            this.form = { ...record }
            this.showDialog = true
        },
        closeDialog() {
            this.showDialog = false
            this.form = {
                petId: null,
                date: '',
                type: '',
                groomingType: '',
                duration: '',
                cost: '',
                notes: ''
            }
        },
        saveRecord() {
            if (this.isEditing) {
                const index = this.groomingRecords.findIndex(r => r.id === this.form.id)
                if (index !== -1) {
                    this.groomingRecords[index] = { ...this.form }
                }
            } else {
                this.groomingRecords.push({
                    ...this.form,
                    id: this.groomingRecords.length + 1
                })
            }
            this.closeDialog()
        },
        deleteRecord(id) {
            if (confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°ËÆ∞ÂΩïÂêóÔºü')) {
                this.groomingRecords = this.groomingRecords.filter(record => record.id !== id)
            }
        }
    }
}
</script>

<style scoped>
.grooming-duration,
.grooming-cost {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #666;
}

.grooming-duration i,
.grooming-cost i {
    color: #9c27b0;
}
</style>